import ProjectHeader from './project/header'
import DateShow from './project/dateShow'
import Module from './project/module'
import axios from 'axios/index'

export default {
  name: 'projectPace',
  components: {
    ProjectHeader,
    DateShow,
    Module
  },
  data () {
    return {
      project: Object,
      name: '',
      module: []
    }
  },
  methods: {
    getPace () {
      sessionStorage.setItem('projectId', this.$route.params.pId)
      axios.get(this.url + '/project/frame/' + this.$route.params.pId)
        .then(this.getPaceSucc)
    },
    getPaceSucc (res) {
      if (res.status !== 200) return null
      else {
        const data = res.data
        this.project = data
        this.module = data.modules
        console.log(this.module)
        this.name = data.name
      }
    }
  },
  created () {
    this.getPace()
  }
}



